import './Header.scss';
import '../styles.scss';
import { ReactComponent as SunIcon } from '../icons/moon.svg';
import { Link } from 'react-router-dom';
import { withRouter } from "react-router-dom";

function Header(props) {

  const toggleDisplayMode = () => props.toggleDisplayMode();
  const getDisplayModeText = () => props.displayMode === 'dark-mode' ? 'Light Mode' : 'Dark Mode';
  const isActiveLink = (match) => {
    const location = props.location.pathname.split('/')[1];
    return (match === location ? 'nav-link-active' : '')
  }

  return (
    <section className="header">
        <div className="main-container">
          <nav className="header-nav">
            <Link to='/projects' className={`nav-link ${isActiveLink('projects')}`}><span>Projects</span></Link>
            <Link to='/about' className={`nav-link ${isActiveLink('about')}`}><span>About</span></Link>
            <button className="nav-link display-mode-toggle" onClick={toggleDisplayMode}>
              <span><SunIcon /><span className="display-mode-text">{` ${getDisplayModeText()}`}</span></span>
            </button>
          </nav>
        </div>
    </section>
  );
}

export default withRouter(Header);
