import './App.scss';
import '../styles.scss';
import { useState } from 'react';
import {Route, Switch } from 'react-router-dom';
import Header from '../Header/Header';
import Hero from '../Hero/Hero';
import Footer from '../Footer/Footer';
import AnimRoutes from '../AnimRoutes/AnimRoutes';

function App() {

  const sessionStorage = window.sessionStorage;
  const [displayMode, setDisplayMode] = useState(sessionStorage.getItem("displayMode") || 'dark-mode');
  const toggleDisplayMode = () => {
    const mode = displayMode === 'dark-mode' ? 'light-mode' : 'dark-mode'
    sessionStorage.setItem("displayMode", mode);
    setDisplayMode(mode);
  }

  return (
    <div className={`app ${displayMode}`}>
      <Header displayMode={displayMode} toggleDisplayMode={toggleDisplayMode}/>
      <Hero/>
      <main>
        <div className="main-container">
        <Switch>
          <Route path="*"><AnimRoutes/></Route>
        </Switch>
        </div>
      </main>
      <Footer />
    </div>
  );
}

export default App;
