import './Card.scss';
import './ProjectCard.scss';
import './AboutCard.scss';
import { ReactComponent as GitHubIcon } from '../icons/github.svg';
import React from 'react';

function Card(props) {
  return (
    <div className={`${props.className ? 'card ' + props.className : 'card'}`} >
      {props.children}
    </div>
  );
}

export function ProjectCard(props) {
  const { title, desc, thumbnails, link, sourceLink, skills } = props.project;

  return (
    <Card className={`${props.className ? 'project-card ' + props.className : 'project-card'}`}>
      <div className='container'>
        <div className='project-image'>
          <img src={thumbnails[0]} alt='project screencap' />
        </div>
        <div className="project-detail">
          <div className="card-title">
            <h2 className="card-header">{title}</h2>
            <a className='project-source' href={sourceLink} target="_blank" rel="noopener noreferrer">{<GitHubIcon />}</a>
          </div>
          <p>{desc}</p>
          <div className="project-skills">
            {skills.map((skill, index) => (
              <span className={`project-skill ${skill}`} key={index}>{skill.toUpperCase()}</span>
            ))}
          </div>
        </div>
        <a className='project-link' href={link} target="_blank" rel="noopener noreferrer"> </a>
      </div>
    </Card>
  );
}

export function AboutCard(props) {
  return (
    <Card className={`${props.className ? 'about-card ' + props.className : 'about-card'}`}>
      <div className='container'>
        <div className="card-title">
          <h2 className="card-header">{props.title}</h2>
          {<props.icon />}
        </div>
        {props.children}
      </div>
    </Card>
  )
}

export default Card;
